
.mint-bread {
  white-space: nowrap;
  font-size: 0;
  width: 100%;
  overflow: auto;
}
.mint-bread > span {
  line-height: 42px;
  display: inline-block;
  position: relative;
  font-size: 14px;
  padding: 0 28px
}
.mint-bread > span.active {
  border-bottom-width: 1px;
  border-bottom-style: solid;
}
.mint-bread > span:after {
  content: '';
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  right: -2px;
  top: 17px;
  border-style: solid;
  border-width: 4px;
}
.mint-bread > span:last-child:after {
  display: none;
}



