
.mint-fixed-button{
  position: fixed;
  border-radius: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 46px;
  height: 46px;
  font-size: 18px;
}
.mint-fixed-button--top-left{
  top: 20px;
  left: 16px;
}
.mint-fixed-button--top-right{
  top: 20px;
  right: 16px;
}
.mint-fixed-button--bottom-left{
  bottom: 20px;
  left: 16px;
}
.mint-fixed-button--bottom-right{
  bottom: 20px;
  right: 16px;
}
.mint-fixed-button--plus{
  position: relative;
  width: 24px;
  height: 24px;
}
.mint-fixed-button--plus_h, .mint-fixed-button--plus_v{
  position: absolute;
  background-color: #fff;
}
.mint-fixed-button--plus_h{
  top: calc(50% - 1px);
  left: 0;
  width: 100%;
  height: 2px;
}
.mint-fixed-button--plus_v{
  top: 0;
  left: calc(50% - 1px);
  width: 2px;
  height: 100%;
}



