
.mint-badge {
    text-align: center;
    display: inline-block;
    border: 1px solid transparent
}
.mint-badge.is-size-large {
    border-radius: 14px;
    font-size: 18px;
    padding: 2px 10px
}
.mint-badge.is-size-small {
    border-radius: 9px;
    font-size: 11px;
    padding: 2px 6px
}
.mint-badge.is-size-normal {
    border-radius: 12px;
    font-size: 15px;
    padding: 2px 8px
}




