
.page-demo {
  padding-bottom: 50px;
}
.page-demo .indexicon {
  font-size: 22px;
  display: inline-block;
  width: 30px;
  vertical-align: middle
}
.page-demo .indexicon.icon-swipe {
  font-size: 26px;
}
.page-demo .indexicon.icon-checklist {
  font-size: 18px;
}
.page-title {
  font-size: 20px;
  margin: 0 auto;
  padding: 20px 0;
  text-align: center;
  display: block;
  line-height: 1;
}
.page-part {
  margin-bottom: 15px;
  background-color: #fff;
}
.page-part:last-child {
  margin-bottom: 0;
}



