
.page-swipe {
    margin-top: 60px;
}
.page-swipe .mint-swipe {
    height: 200px;
    font-size: 30px;
    text-align: center;
    margin-bottom: 20px;
}
.page-swipe .mint-swipe-item {
    line-height: 200px;
}
.page-swipe .slide1 {
    background-color: #D8D8D8;
    color: #fff;
}
.page-swipe .slide2 {
    background-color: #D8D8D8;
    color: #000;
}
.page-swipe .slide3 {
    background-color: #D8D8D8;
    color: #fff;
}
.page-swipe .swipe img {
    display: block;
    height: 100%;
    width: 100%;
}
.page-swipe-desc {
    text-align: center;
    color: #1E2329;
    margin-bottom: 5px;
}



