
.bh-ddb {
    overflow: auto;
    padding: 13px 0 12px;
    border-bottom-width: 1px;
    border-top-width: 1px;
    border-bottom-style: solid;
    border-top-style: solid;
}
.bh-ddb-border-bottom{
  border-bottom:none;
}
.bh-ddb-item {
    float:left;
    font-size: 13px;
    position: relative;
    box-sizing: border-box;
    border-right-width: 1px;
    border-right-style: solid;
    text-align: center;
    padding-right: 6px;
}
.bh-ddb .bh-ddb-item:last-child {
    border-right:none;
}
.bh-ddb-item label {
    position: relative;
    display: inline-block;
    max-width: 70%;
    margin: 0 auto;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    vertical-align: top;
    text-align: center;
}
.bh-ddb-i {
    position: relative;
    top: 0px;
    left: 6px;
    font-size: 0px;
}
.bh-ddb-i-default {
    width: 0;
    height: 0;
    border-left: 3.5px solid transparent;
    border-right: 3.5px solid transparent;
    border-top-width: 4px;
    border-top-style: solid;
}
.bh-ddb-i-selected {
    width: 0;
    height: 0;
    border-left: 3.5px solid transparent;
    border-right: 3.5px solid transparent;
    border-bottom-width: 4px;
    border-bottom-style: solid;
    top:-4px !important;
}
.bh-ddm {
  position: absolute;
  width: 100%;
  z-index: 2000;
  overflow: auto;
}
.bh-ddm-shadow {
  width: 100%;
  z-index: 1999;
  position: fixed;
  top:0;
  left:0;
  right:0;
  bottom: 0;
}
.bh-ddm .mint-hairline--top-bottom::after{
  border-top-width: 0;
}
.bh-radio-slot .mint-radiolist-title {
  margin: 0;
}
.bh-ddm .mint-cell {
  min-height: 42px;
}
.bh-ddm .mint-cell-allow-right {
  display: none;
}



