
.mint-navbar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    width: 100%;
    overflow-x: auto;
}
.mint-navbar .mint-tab-item {
    padding: 14px 0 13px;
    font-size: 14px;
    border-bottom-width: 1px;
    border-bottom-style: solid
}
.mint-navbar .mint-tab-item:last-child {
    border-right: 0;
}
.mint-navbar .mint-tab-item.is-selected {
    border-bottom-width: 2px;
}
.mint-navbar.is-fixed {
    top: 0;
    right: 0;
    left: 0;
    position: fixed;
    z-index: 1001;
}



